body{
    color:ivory;
    padding: 0;
    margin: 0;
    font-size: 2.5rem;
    background-color: #161616;
}

#back-home{
    float: right;
}

.header .row{
    padding: 20px 0px 0px;
    display: flex;
    flex-flow: wrap;

}
.header .section-heading.page-heading{
    margin-bottom: 83px;
    text-align: center;
}

.header .section-heading .section-description{
    font-size: 1.05rem;
    margin-bottom: 17px;
}

.header .row .animated-bar{
    margin-top: 10px;
    background-color: #009e66;
    content: '';
    display: block;
    height: 4px;
    left: 0;
    margin: 0px auto 0px auto;
    position: relative;
    right: 0;
    width: 100%;
    justify-content: center;
}

p{
    font-size: 15px;
}
h1{
    font-size: 2.5rem;
    font-weight: bold;
}

/* Skill */


.my-skill-know .single-skill .skill-info {
    -ms-flex-pack: justify;
    color: #cecece;
    display: -ms-flexbox;
    display: flex;
    font-size: 16px;
    justify-content: space-between;
    margin-bottom: 4px;
}

.my-skill-know .single-skill .skill-progress {
    background-color: #2f2f2f;
    border-radius: 1px;
    font-size: 15px;
    height: 9px;
    overflow: visible;
}

.my-skill-know .single-skill{
    margin-bottom: 42px;
}

.my-skill-know .single-skill .skill-progress .progress-bar {
    background-color: #009e66;
}

.my-skill-know .single-skill .skill-progress .progress-bar {
    border-bottom-left-radius: 1px;
    border-top-left-radius: 1px;
}

@media(min-width: 768px){
    .col-md-6 {
        -ms-flex: 0 0 50%;
        flex: 0 0 50%;
        max-width: 50%;
    }
}
